import React, { useContext } from 'react'
import styled from 'styled-components'
import { GlobalContext } from '../../context'
import { PORTFOLIO_PATH, CONTACT_PATH } from '../../helpers/utils'
import { MdPhotoLibrary, MdEmail } from 'react-icons/md'
import { AiFillHome } from 'react-icons/ai'

const Menu = ({
    close,
    menuRef,
}: {
    close?: () => void
    menuRef?: React.RefObject<HTMLUListElement>
}) => {
    const { content, isDesktop, isMobile } = useContext(GlobalContext)
    const MENU_DATA = [
        { href: '#', title: content.nav.home, icon: <AiFillHome /> },
        {
            href: PORTFOLIO_PATH,
            title: content.nav.portfolio,
            icon: <MdPhotoLibrary />,
        },
        {
            href: CONTACT_PATH,
            title: content.nav.contact,
            icon: <MdEmail />,
        },
    ]

    return (
        <MenuWrapper>
            <List ref={menuRef}>
                {MENU_DATA.map(link => (
                    <ListItem key={link.title}>
                        <Link href={link.href} onClick={close}>
                            <LinkContent>
                                {((!isDesktop && !isMobile) ||
                                    isMobile) &&
                                    link.icon}
                                <span style={{ marginLeft: '.5em' }}>
                                    {link.title}
                                </span>
                            </LinkContent>
                        </Link>
                    </ListItem>
                ))}
            </List>
        </MenuWrapper>
    )
}
export const MenuWrapper = styled.div`
    text-align: center;
    @media only screen and (min-width: 900px) {
        height: 100%;
        padding: 0 2em;
    }
`
export const List = styled.ul`
    width: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
    z-index: 5;
    background: rgba(255, 255, 255, 0.2);

    @media only screen and (min-width: 900px) {
        background: none;
        flex-direction: row;
        position: relative;
        height: 100%;
    }
`
export const Link = styled.a`
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
`
export const LinkContent = styled.span`
    display: flex;
    justify-content: flex-start;
    align-items: center;
    text-align: center;

    padding: ${({ theme }) => theme.padding.medium} 0;
    margin: 0 auto;
    width: 100%;
    max-width: 110px;
    font-weight: 600;
    transition: color 0.4s;
    @media only screen and (min-width: 900px) {
        max-width: 150px;
        padding: 0 ${({ theme }) => theme.padding.xxl};
        &:hover {
            color: ${({ theme }) => theme.colors.primary};
        }
    }
`

export const ListItem = styled.li`
    @media only screen and (max-width: 900px) {
        background: transparent;
        transition: background-color 0.3s;
        &:hover {
            background: ${({ theme }) => theme.colors.primary};
        }
    }
`

export default Menu
